import React, {Component} from 'react';
import Footer from "./Footer";

class ContactUs extends Component {
    render() {
        return (
          <React-Fractment>
              <div
                className="w3-content w3-text-grey contact"
                id="contact"
              >
                <h2 className="w3-text-light-grey w3-margin-left">
                  Contact Us Today
                </h2>
                <hr className="w3-opacity" />

                <div className="w3-section">
                  <p className="w3-margin-bottom w3-margin-right w3-margin-left">
                    <i className="fa fa-map-marker fa-fw w3-text-white w3-xxlarge w3-margin-right"></i>
                    Chicago, US
                  </p>
                  <p className="w3-margin-bottom w3-margin-right w3-margin-left">
                    <i className="fa fa-phone fa-fw w3-text-white w3-xxlarge w3-margin-right"></i>
                    Phone: +00 151515
                  </p>
                  <p className="w3-margin-bottom w3-margin-right w3-margin-left">
                    <i className="fa fa-envelope fa-fw w3-text-white w3-xxlarge w3-margin-right"></i>
                    Email: mail@mail.com
                  </p>
                </div>
                <br />
                <p className="w3-margin-bottom w3-margin-right w3-margin-left">
                  Lets get in touch. Send me a message:
                </p>

                <form action="/action_page.php">
                  <p className="w3-margin-bottom w3-margin-right w3-margin-left">
                    <input
                      className="w3-input w3-padding-16"
                      type="text"
                      placeholder="Name"
                      required
                      name="Name"
                    />
                  </p>
                  <p className="w3-margin-bottom w3-margin-right w3-margin-left">
                    <input
                      className="w3-input w3-padding-16"
                      type="text"
                      placeholder="Email"
                      required
                      name="Email"
                    />
                  </p>
                  <p className="w3-margin-bottom w3-margin-right w3-margin-left">
                    <input
                      className="w3-input w3-padding-16"
                      type="text"
                      placeholder="Subject"
                      required
                      name="Subject"
                    />
                  </p>
                  <textarea
                    className="text-area-width w3-margin-bottom w3-margin-right w3-margin-left w3-input w3-padding-16"
                    rows="9"
                    cols="30"
                    placeholder="Message"
                    required
                    name="message"
                  ></textarea>
                  <p className="w3-margin-bottom w3-margin-right w3-margin-left">
                    <button
                      className="w3-button w3-light-grey w3-padding-large"
                      type="submit"
                    >
                      <i className="fa fa-paper-plane"></i> SEND MESSAGE
                    </button>
                  </p>
                </form>
              </div>
            <Footer />
          </React-Fractment>
        );
        }
    }

export default ContactUs;
