import React, {Component} from 'react';

const Motivation = () => {
    return (
        <section className="motivation_container">
            <article className="motivation_content">
                <div className="motivation_content--back">
                    <h4 className="motivation_content--title">
                        WHY CHOOSE YOUR PUPPY FROM US ?
                    </h4>
                    <p className="motivation_content--body">
                        Our puppies are raised in a clean and sterelized, odor free home, and have plenty of playtime both indoors and out with our children and our little cat Milo (weather permitting) in a clean yard.
                    </p>
                    <p className="motivation_content--body">
                        Our dogs are family. They are loving, friendly, playful, lively, and happy. They are fed a high quailty raw diet along with a premium kibble. Their treats are homemade, I do not feed my dogs processed treatsThey have regular vet care, and lovely, spacious heated and air- conditioned buildings. The dogs run in large yards with free access to go in and out as they please. The premises are kept cleaned and sanitized. The dogs all have regular inside family time. We don't see our dogs as animals but just like our own intimate family just like our own kids.
                    </p>
                    <p className="motivation_content--body">
                        I am here after you take your puppy home, and am happy to say i have been fortunate to make many long -time friends with my puppy buyers. Buyers are welcome to visit anytime. We will always asist you in anyway we can to make sure your puppy remain healthy.
                    </p>
                </div>
            </article>
        </section>
    );
}

export default Motivation;