import React from 'react';
import HomeMain from "./sub-home-components/HomeMain";
import HomeAvailaibles from "./sub-home-components/HomeAvailaibles";



const Home = () => {
    return (
    <React-Fragment>
    <section className="home">
          <div class="header__text-box flex-items">
            <h1 class="heading-primary">
              <span class="heading-primary--main">home for pugdogs</span>
              <span class="heading-primary--sub">We gadly welcome you</span>
            </h1>
            <a href="./#availaibles" className="btn btn--white btn--animated">discover our Puppies</a>
          </div>
     </section>
        <HomeMain />
        <HomeAvailaibles isChecked />
      </React-Fragment>
    );
  }

export default Home;
