import React, {Component} from "react";
import {NavLink} from "react-router-dom";

class HomeMain extends Component {
    constructor(props) {
        super(props);
        this.state = {
            warranty: [
                {
                class: "assure-delivery",
                rating: "100%",
                type: "shipping",
                action: "delivery",
                detail: "Our Pug dog Puppies are shipped with most major airlines and delivery agencies, they all have pressurized cabins with climate control systems, the Puppies are flown in a special compartment for pets"
                },
                {
                class: "assure-delivery",
                rating: "100%",
                type: "health",
                action: "guaranty",
                    detail: "We take pride in the fact that our relationship with you does not end when you take home a puppy from us. As die-hard Pug dog lovers ourselves, we’d like to believe nothing can ever go wrong with our puppies."
                }
            ]
        };
    }
    

    render() {
        return(
            <main class="body_home">
                <section className="about-pug">
                    <div className="about-pug-content">
                        <span className="pug-caption"></span>
                        <div class="pug-caption--detail">
                            <h2 className="pug-caption-head">
                                Top Quality Pug Puppies, Priceless Puppies
                            </h2>
                            <p>
                            The Pug is a small, stocky, square, thickset dog. The round head is massive with a short, blunt, square-shaped muzzle. Moles on the cheeks are considered beauty spots. The teeth meet in a slight undershot bite. The very large, prominent eyes are dark.
                            The small, thin ears are either rose or button shaped. The face has large, deep wrinkles. The high-set tail is curled over the back and a double curl is preferred in the show ring. Dewclaws are usually removed. The short coat is soft,
                            fine and smooth. Coat colors come in apricot, fawn, black and silver. <NavLink to="/about-us" className="home-anchor-about"><span>Read More About Us</span> &#8594;</NavLink>
                            </p>
                        </div>
                    </div>
                </section>
                <section className="warranty">
                    {this.state.warranty.map((input) => 
                    <h2 className={input.class}>
                        <span>{input.rating}</span>
                        <span>{input.type}</span>
                        <span>{input.action}</span>
                        <p>{input.detail}</p>
                    </h2>
                    )}
                </section>
            </main>
        )
    }
}

export default HomeMain;