import React, {Component} from "react";
import {NavLink} from "react-router-dom";

const OrderButton = () => {
    return(
        <section className="orderbutton_section">
            <NavLink to="/contact-us" className="orderbutton_section-btn">
                Place An Order &#8594;
            </NavLink>
        </section>
    );
}

export default OrderButton;