import React, {Component} from 'react';
import KcPug from "../assests/images/kc-pug.jpg";
import Footer from './Footer';

class Feedbacks extends Component {
    constructor(props) {
        super(props);
        this.state = {
            responses: [
                {
                    figsrc: KcPug,
                    figcap: "Lee",
                    typocont: " Simphi is eating very well!! I took her to the vet yesterday, and they said she was very health. I feed her 4 times a day. She loves to play with people and sleep on the couch in the living room. I am also planning to send her to a pet kindergarten in few weeks to come. I hope she will enjoy at the kindergarten. She is really adorable, and she loves people. she became a super star in my APT.Thanks you once more for letting me have her. I adore here",
                },
                {
                    figsrc: KcPug,
                    figcap: "Stephany Mancel",
                    typocont: " Izzy, the Pug puppy is enjoying her 1st birthday! \
                     Hi Agile Pug Home, Tigriel turned 1 yr old. She tolerated the hat but loved the cake. She has been a great addition to our family. She is so cute, funny and smart. She has learned to open the back door to let herself back in.\
                     She is awesome.We just love her.Thanks again,",
                   
                },
                {
                    figsrc: KcPug,
                    figcap: "Elizabeth Hooks",
                    typocont: "  I just want to send you a quick email for a little update for gena!!! She went for her 12 week vaccinations today!! She is very healthy! gena is doing really good in our home and is loved and played with tons!! She is stubborn about a few things but I already have a trainer coming into our home working with us!. gena loves belly rubs and loves sun tanning in the sun! She is so special to our family and I just want to tell you how thankful I am to have such a great puppy!!",
                   
                },
                {
                    figsrc: KcPug,
                    figcap: "Handeson and Passy Jr.",
                    typocont: " Just wish to let you guys know how much we love our Baby Bonna. He's so so so sweet and gorgeous. We so much adore him!! He's very tiny just like you predicted. Already 8 months old and only 12.4 lbs. We're so thrilled with him and we thank you so very much for bringing him to us. We'll send you some pics of him when we go for vaccination by weekend.Best Regards,",
                   
                },
                {
                    figsrc: KcPug,
                    figcap: "Jeofrey Schnieder",
                    typocont: " (Puppy Arrival) Puppy arrived well. He sure is a fine puppy. Thank you very much. Month After) I took the puppy to the vet yesterday to have him check out and get his parvo shot. The vet just happen to own a Pug himself. He told me that the puppy come from a good bloodline and was a very good looking puppy. So on that note I just wanted to thank you very much again. ",
                   
                },
                {
                    figsrc: KcPug,
                    figcap: "Merchi Arnold",
                    typocont: " I changed her name to Bella Mimi Koon. The name really fits her personality as she is the bell of the ball everywhere we travel. We've been on the train and bus and people take pics with their cell phones, one woman chased me in the grocery store just to pet her :) The local pet store has been very helpful and she's starting to use her wee wee pads!! I find she's very smart and loves human contact and play time. , thank you for all the help, information and extremely professional packaging with her records. She arrived a little nervous but quickly warmed up as soon as we made ",
                   
                },
                {
                    figsrc: KcPug,
                    figcap: "Lawrence, Esthel and family…. ",
                    typocont: " Hello Agile Pug Home I can’t tell you how happy.  Barbie and Carrie are great puppies. They are a fabulous dog and we love them very much. We started dog training this week and they are proving to be a very smart girl and boy. They certainly have their own personality, but they are real members of the family and love to sit with us in the evenings watching TV. They are learning to run beside Andy and I and has had a few introductions to the dog park around the corner. They have certainly grown a little since leaving you. I enjoy them very much. We are quite thrilled with the new addition to our pack!",
                   
                },
                {
                    figsrc: KcPug,
                    figcap: "Olice",
                    typocont: " Hi there, Our  Puppy has turned out to be an excellent dog. You missed the boat on this one with regards to his intelligence. He learns and picks up things extremely quickly. He’s more intelligent that Coal was. I would love to train and campaign him in obedience, but that’s not going to happen as I don’t want to take away from the relationship Damsto and Edam has with him. ",
                   
                }


            ]
        };
    }
    render() {
        return (
        <React-Fracment>
        <div className="feedback w3-medium">
            <header className="available_page--head"> We've Got Some Testionies for You </header>
            <p className="w3-conatin w3-center w3-padding-large w3-content w3-text-grey">Now and then we receive emails from families who have gotten their puppy from us.
            We absolutely love to keep in touch with owners of our previous puppies and we’ve even had the joy of babysitting one of our puppies (Gus) while his family was out-of-town. Here is a short list of testimonies from people who have chosen us as their Pug breeder – We know them as “friends and family”</p>
            {this.state.responses.map( ev => 
                <div className="w3-container w3-content w3-card-4">
                    <div className="w3-quarter">
                        <div class="w3-white w3-text-grey">
                            <figcaption className={ev.figcapclass}>
                                <div className="date">{ev.date}</div> <div className="date">{ev.figcap}</div>  
                            </figcaption>
                            <img src={ev.figsrc} alt="testifier" className="figimg" />
                        </div>
                    </div>
                    <div className="w3-threequarter">
                        <p className="typoclass w3-text-grey">
                            {ev.typocont}
                        </p>
                    </div>
                </div>)}
        </div>
        <p className="w3-conatin w3-padding-large w3-content w3-small">THERE ARE MANY MORE TESTIMONIALS OF PEOPLE WHO  DERIVED SATISFACTION FROM US , WE HOPE YOURS WILL    CERTAINLY FIT IN HERE AFTER YOU MUST HAVE BOUGHT YOUR OWN PUPPY.
        <br />
            THANKS FOR YOUR TIME
        </p>
        <Footer />
        </React-Fracment>
        );
    }
}

export default Feedbacks;
