"throwIfNamespace"
import React, {Component} from "react";
import {NavLink} from "react-router-dom";
import kcPug from "../../assests/images/kc-pug.jpg";
import kcPug1 from "../../assests/images/kc-pug1.jpg";
import kcPug2 from "../../assests/images/kc-pug2.jpg";
import kcPug3 from "../../assests/images/kc-pug3.jpg";
import kcPug4 from "../../assests/images/kc-pug4.png";
import kcPug5 from "../../assests/images/kc-pug5.jpg";
import kcPug6 from "../../assests/images/kc-pug6.jpg";
import Motivation from "./HomeAvailaibleMotiv";
import Footer from '../Footer';
import OrderButton from './HomeAvailaibleOrder';


class HomeAvailaibles extends Component {
    constructor(props){
        super(props);
        this.state = {
          set1: [
            {
              path: "/availiable-pugs",
              src: kcPug,
              caption:
                "little discription caption for the available pup goes in here",
              alt: "pet disc"
            },
            {
              path: "/availiable-pugs",
              src: kcPug1,
              caption:
                "little discription caption for the available pup goes in here",
              alt: "pet disc"
            },
            {
              path: "/availiable-pugs",
              src: kcPug2,
              caption:
                "little discription caption for the available pup goes in here",
              alt: "pet disc"
            },
            {
              path: "/availiable-pugs",
              src: kcPug6,
              caption:
                "little discription caption for the available pup goes in here",
              alt: "pet disc"
            },
            {
              path: "/availiable-pugs",
              src: kcPug3,
              caption:
                "little discription caption for the available pup goes in here",
              alt: "pet disc"
            },
            {
              path: "/availiable-pugs",
              src: kcPug4,
              caption:
                "little discription caption for the available pup goes in here",
              alt: "pet disc"
            },
            {
              path: "/availiable-pugs",
              src: kcPug5,
              caption:
                "little discription caption for the available pup goes in here",
              alt: "pet disc"
            },
            {
              path: "/availiable-pugs",
              src: kcPug5,
              caption:
                "little discription caption for the available pup goes in here",
              alt: "pet disc"
            }
          ]
        };
    }
    render() {
        return (
          <section className="availaible_home" id="availaibles">
            <Motivation />
            <h3 className="availaible_title">Available Pups</h3>
            <div className="availiable_home--set1">
              {this.state.set1.map(value => (
                <NavLink to={value.path} className="avaiaible_home--link">
                  <figure className="availaible_home--figures">
                    <img
                      src={value.src}
                      alt={value.alt}
                      className="availaible_home--img"
                    />
                    <figcaption className="availaible_home--img-cap">
                      {value.caption}
                    </figcaption>
                  </figure>
                </NavLink>
              ))}
            </div>
            <OrderButton />
            <Footer />
          </section>
        );
    }

}

export default HomeAvailaibles;