import React, {Component} from 'react';
import { NavLink } from 'react-router-dom';

class Navigation extends Component {
    constructor(props) {
        super(props);
        this.state = {
            navDir: [
                { path: "/", value: "Agile Pug Home" },
                { path: "/", value: "Home" },
                { path: "/about-us", value: "About-Us" },
                { path: "/availiable-pugs", value: "Availiable-Pugs" },
                { path: "/testimonies", value: "Testimonials" },
                { path: "/contact-us", value: "Contact-Us" }
            ],
            isChecked: false,
            };
        this.checkBox = this.checkBox.bind(this);
        this.unCheckBox = this.unCheckBox.bind(this);
        this.handleCheckToggle = this.handleCheckToggle.bind(this);
    }
    checkBox() {
        (this.state.isChecked === false)? 
        this.setState({ isChecked: true }) : this.setState({isChecked: false});
    }

    unCheckBox() {
        this.setState({
            isChecked: false
        });
    }
    handleCheckToggle(evt) {
        if(this.state.isChecked === true) {
            this.setState({
                isChecked: false
            });
        }
        else if(this.state.isChecked === false) {
            this.setState({
                isChecked: true
            });
        }
    }


    render() { 
        return (
            <div className="nav_container">
                <input type="checkbox" id="mobile_menu--gen" name="mobile-menu--gen" checked={this.state.isChecked} onChange={this.handleCheckToggle} />
                <div className="top_bar"><span>&#9742; +00 151515</span></div>
                <div className="desktop_nav">
                    {this.state.navDir.map(Value => <NavLink  className="nav_items-desktop" to={Value.path}><span>{Value.value}</span></NavLink> )}
                    <label class="mobile_menu--btn" onClick={this.checkBox}>
                        <span></span>
                        <span></span>
                        <span></span>
                    </label>
                </div>
                <div className="menu_container--mobile">
                    <nav class="menu_mobile">
                        {this.state.navDir.map(Value => <NavLink className="menu_mobile--items" to={Value.path} onClick={this.unCheckBox}><span>{Value.value}</span></NavLink>)}
                    </nav>
                </div>
                <div className="top_bar"></div>
            </div>
        );
    }
}

export default Navigation;
